import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';
import { SharedModule } from '@checkcars/shared';
import { AdvertisementModule } from '@checkcars/widgets';
import { LeasingOrFinancingComponent } from './components/page/leasing-or-financing/leasing-or-financing.component';

const routes: Routes = [{ 
  path: '', component: LeasingOrFinancingComponent 
}];

@NgModule({
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    HttpClientModule,
    SharedModule,
    AdvertisementModule,
  ],
  declarations: [LeasingOrFinancingComponent],
  exports: [LeasingOrFinancingComponent],
})
export class LeasingOrFinancingModule {}
